body{
  background-image: url(./img/back.jpg);
  background-attachment: fixed;
  background-position: bottom;
}

.image{
  object-fit: cover;
}

.input{
  max-width: 100%;
  border-radius: 0;
  border: 0;
  box-shadow: none;
  padding: 20px;
  outline: none;
  transition: 0.6s;
  border-radius: 10px 0px 0px 10px;
  border-bottom: 3px solid white;
  padding-bottom: 17px;
}

.input:focus{
  background-color: transparent;
  color: white;
  border-bottom: 3px solid green;
  padding-bottom: 17px;
  box-shadow: none;
  padding-right: 50px;
  padding-left: 50px;
  transition: 0.8s;
  
}

.app .search{
  background: green;
  padding: 20px 30px;
  border-radius: 0;
  border: 0;
  margin-left: -10px;
  transition: 0.6s;
  border-radius: 0px 10px 10px 0px;
}

.app .search:hover{
  background: rgb(0, 255, 0);
  border-radius: 0px 10px 10px 0px;
  color: rgba(0, 0, 0, 0.778);
  padding: 20px 30px;
  border: 0;
  transition: 0.6s;
}